.main {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.main__date-box {
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.main__date-calendar {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: auto;
}

.main__date-calendar h1 {
    font-size: calc(30 * (100vw / 1080));
    font-weight: 500;
    color: #2D4047;
}

.main__date-date {
    font-weight: 600;
    color: #05B98C;
    position: relative;
}

.main__date-date::before {
    content: '';
    bottom: -4px;
    left: 0px;
    width: 100%;
    height: 4px;
    background-color: #05B98C;
    position: absolute;
}

.main__calendar-box {
    width: 100%;
    height: 76%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main__calendar {
    background-color: #F7FAFC;
    padding: 2vh;
    border-radius: 25px;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: repeat(4, 1fr);
    gap: 2vh;
}

.main__cell-dates {
    background-color: #F0F4F6;
    border-radius: 10px;
    padding: 1vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    transition-duration: 0.2s;
    animation: born;
    animation-duration: 0.6s;
    animation-fill-mode: backwards;
    animation-timing-function:cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

@keyframes born {
    0% {
        transform-origin: 50% 50%;
        transform: scale(0.1);
    }

    100% {
        transform: scale(1);
    }
}

.main__cell-dates:hover {
    cursor: pointer;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
    transform: scale(1.05);
}

.main__d-year {
    color: #BCC3C3;
    font-weight: 400;
    font-size: calc(8 * (100vw / 1080));
    ;
}

.main__d-number {
    color: #7C8989;
    font-weight: 500;
    font-size: calc(26 * (100vw / 1080));
}

.main__d-tag {
    color: #7F8C8C;
    font-size: calc(8 * (100vw / 1080));
    font-weight: 500;
}

.main__cell-dates-now {
    background-color: white;
    border-radius: 10px;
    padding: 1vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
    transition-duration: 0.2s;
    animation: born 0.6s;
    animation-fill-mode: backwards;
}

.main__cell-dates-now:hover {
    cursor: pointer;
    transform: scale(1.05);
}

.main__d-number-now {
    font-weight: 500;
    font-size: calc(26 * (100vw / 1080));
    color: #05B98C;
}

.main__data-changer-box {
    width: 100%;
    height: 14%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.main__data-changer-body {
    height: 8vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: #F7FAFC;
    border-radius: 50px;
    padding: 1vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main__date-change {
    font-size: calc(22 * (100vw / 1080));
    ;
    color: #05B98C;
    font-weight: 500;
}

.main__button-change-left,
.main__button-change-right {
    background-color: white;
    height: 100%;
    border-radius: 50%;
    width: 6vh;
    box-shadow: 0px 4px 6px #BECFCB;
}

.main__button-change-left,
.main__button-change-right {
    display: flex;
    justify-content: center;
    align-items: center;
    transition-duration: 0.2s;
}


.main__button-change-left:hover,
.main__button-change-right:hover {
    transform: scale(1.1);
}

.main__button-change-right img,
.main__button-change-left img {
    width: 40%;
}

.main__button-change-right img {
    transform: rotate(180deg);
}